<template>
  <div id="personal-settings">
    <div class="mk-panel">
      <div class="mk-panel-header">
        <h3 class="mk-panel-title">
          Change password
          <hint-question :text="hints.changePassword" />
        </h3>
      </div>
      <div class="mk-panel-body">        
        <form class="mk-form">
          <pre class="api-result"></pre>
          <password-input ref="oldPassword" title="Old password" /> 
          <password-input ref="newPassword" title="New password" /> 
          <password-input ref="newPasswordRe" title="Repeat new password" />
          <vue-recaptcha
            ref="changePasswordReCaptcha"
            class="reCaptcha"
            @verify="on_chpr_verify"
            @expired="on_chpr_expired"
            :sitekey="sharedStore.reCaptchaSiteKey">
          </vue-recaptcha>
          <button class="mkbtn mkbtn-primary" type="button" @click.prevent="changePassword">Save changes</button>
        </form>
      </div>
    </div>
    <div class="mk-panel">
      <div class="mk-panel-body">
        <form class="mk-form">
          <div class="form-input">
            <checkbox label="Two-factor authorization" :onChange="twoFactorChanged" />
            <div class="clearfix"></div>

            <div class="two-factor-cunfirm" v-if="twoFactorQr">
              <span>For use two-factor authorization please install Google Authenticator app on you phone and scan QR to setup 2FA access to the site</span>
              <div class="app-links">
                <a href="https://play.google.com/store/apps/details?id=com.google.android.apps.authenticator2&hl=en" target="_blank">
                  <img src="/static/img/google-play.png" />
                </a>
                <a href="https://itunes.apple.com/us/app/google-authenticator/id388497605?mt=8" target="_blank">
                  <img src="/static/img/app-store.png" />
                </a>
              </div>
              <img :src="twoFactorQr" :style="{ width: getWidthForCm(4) + 'px' }" />

              <div class="form-input">
                <input name="googleAuthCode" v-model="googleAuthCode" type="text" placeholder="Enter the Google Authenticator code" autocomplete="on" />
              </div> 
              
              <vue-recaptcha
                ref="reCaptcha2fa"
                class="reCaptcha"
                @verify="on_2fa_verify"
                @expired="on_2fa_expired"
                :sitekey="sharedStore.reCaptchaSiteKey">
              </vue-recaptcha>
              <button class="mkbtn mkbtn-primary" type="button" @click.prevent="twoFactorConfirm">Save changes</button>
            </div>

          </div>
          <div class="form-input">
            <checkbox label="Forbid TOR access" :onChange="forbidTORAccessChanged" />
            <div class="clearfix"></div>
          </div>
        </form>
      </div>
    </div>
    <div class="mk-panel">
      <div class="mk-panel-header">
        <h3 class="mk-panel-title">
          Change E-mail
          <hint-question :text="hints.changeEmail" />
        </h3>
      </div>
      <div class="mk-panel-body">        
        <form class="mk-form">
          <pre class="api-result"></pre>
          <password-input ref="password" title="Password" /> 
          <div class="form-input">
            <label>New E-mail</label>
            <input type="Email" v-model="changeEmailForm.email" />
          </div>
          <vue-recaptcha
            ref="changeEmailReCaptcha"
            class="reCaptcha"
            @verify="on_cher_verify"
            @expired="on_cher_expired"
            :sitekey="sharedStore.reCaptchaSiteKey">
          </vue-recaptcha>
          <button class="mkbtn mkbtn-primary" type="button" @click.prevent="changeEmail">Save changes</button>
        </form>
      </div>
    </div>

    <div class="mk-panel">
      <div class="mk-panel-header">
        <h3 class="mk-panel-title">
          Security
        </h3>
      </div>
      <div class="mk-panel-body">        
        <form action="" class="mk-form">
          <pre class="api-result"></pre>
          <password-input ref="resetEmailPassword" title="Password" /> 
          <div class="form-input">
            <label>Password reset E-Mail address (blank to disable resets):</label>
            <input type="Email" v-model="securityForm.email" />
          </div>
          <vue-recaptcha
            ref="resetEmailPasswordReCaptcha"
            class="reCaptcha"
            @verify="on_repr_verify"
            @expired="on_repr_expired"
            :sitekey="sharedStore.reCaptchaSiteKey">
          </vue-recaptcha>
          <button class="mkbtn mkbtn-primary" type="button" @click.prevent="changeResetEmail">Save changes</button>
        </form>
      </div>
    </div>

    <div id="panel-api-key" class="mk-panel">
      <div class="mk-panel-header">
        <h3 class="mk-panel-title">
          API key
          <hint-question :text="hints.apiKey" />
        </h3>
      </div>
      <div class="mk-panel-body">
        <p class="normal">An API key allows third-party software to retrieve statistics associated with your account.</p>
        <p class="normal">API keys are read-only and cannot be used to modify account settings.</p>
        <br />
        <h5>Your API key:</h5>
        <div class="mk-info wrap-words">
          1b337694053220befdd79c4aa0763fa107ed5099f9467e0fe61d387c5a4aa7f2
        </div>
      </div>
    </div> <!-- mkpanel apikey -->

  </div>
</template>

<script>
import { Utils } from '@/utils';
import { WebApi } from '@/webapi';
import { SharedStore } from '@/shared-store';
import VueRecaptcha from 'vue-recaptcha';
import PasswordInput from '@/components/elements/PasswordInput';
import HintQuestion from '@/components/elements/HintQuestion';
import Checkbox from '@/components/elements/Checkbox';

export default {
  components : {
    HintQuestion,
    PasswordInput,
    VueRecaptcha,
    Checkbox
  },
  data() {
    return {
      sharedStore : SharedStore.state,
      hints : {
        changePassword : "No text",
        changeEmail : "No text",

        apiKey : "No text"
      },
      changePasswordForm : {
        oldPassword : null,
        newPassword : null,
        newPasswordRe : null,
        captcha: null
      },
      changeEmailForm : {
        password : null,
        email : null,
        captcha : null
      },
      securityForm : {
        password : null,
        email : null,
        captcha : null
      },
      twoFactorCaptcha : null,
      twoFactorQr : null,
      googleAuthCode: null
    }
  },
  methods : {
    getWidthForCm(cm_value) {
      /* cm = px * 2.54 / dpi; */
      return cm_value * (window.devicePixelRatio * 96) / 2.54;
    },
    on_chpr_verify(r) {
      this.changePasswordForm.captcha = r;
    },
    on_chpr_expired() {
      this.changePasswordForm.captcha = null;
    },

    on_cher_verify(r){
      this.changeEmailForm.captcha = r;
    },
    on_cher_expired(){
      this.changeEmailForm.captcha = null;
    },

    on_repr_verify(r){
      this.securityForm.captcha = r;
    },
    on_repr_expired(){
      this.securityForm.captcha = null;
    },

    on_2fa_verify(r){
      this.twoFactorCaptcha = r;
    },
    on_2fa_expired(){
      this.twoFactorCaptcha = null;
    },

    changePassword(e) {
      let self = this;
      let target = e.currentTarget;

      self.$refs.changePasswordReCaptcha.reset();

      this.changePasswordForm.oldPassword = this.$refs.oldPassword.getValue();
      this.changePasswordForm.newPassword = this.$refs.newPassword.getValue();
      this.changePasswordForm.newPasswordRe = this.$refs.newPasswordRe.getValue();
      
      WebApi.changePassword(this.changePasswordForm, function(r){
        $(target).parent().find('.api-result').html(Utils.formatJson(r));
      });
    },
    changeEmail(e) {
      let self = this;
      let target = e.currentTarget;
      
      self.$refs.changeEmailReCaptcha.reset();
      self.changeEmailForm.password = this.$refs.password.getValue();

      WebApi.changeEmail(this.changeEmailForm, function(r){
        $(target).parent().find('.api-result').html(Utils.formatJson(r));
      });
    },
    changeResetEmail(e) {
      let self = this;
      let target = e.currentTarget; 

      self.$refs.resetEmailPasswordReCaptcha.reset();
      self.securityForm.password = this.$refs.resetEmailPassword.getValue();

      WebApi.changePasswordResetEmail(this.securityForm, function(r){
        $(target).parent().find('.api-result').html(Utils.formatJson(r));
      })
    },

    twoFactorChanged(value) {
      let self = this;
      if (value === true) {
        WebApi.turn_2fa_on(function(r){
          if (r.isSuccess) {
            self.twoFactorQr = r.data.base64image;
          }
        });
      }
      if (value === false) {
        WebApi.turn_2fa_off(function(r){
          if (!r.isSuccess) {
            alert(r.message);
          }
        })
      }
    },

    twoFactorConfirm() {
      let self = this;

      self.$refs.reCaptcha2fa.reset();

      if (self.twoFactorCaptcha && self.googleAuthCode) {
        WebApi.turn_2fa_on_confirm({ code: self.googleAuthCode, captcha:self.twoFactorCaptcha }, 
          function(r){
            if (r.isSuccess) {
              self.twoFactorQr = null;
            } else {
              alert(r.message);
            }
          });
      }
    },
    forbidTORAccessChanged(value) {
      let self = this;
      WebApi.prevent_tor(value, function(r){
        if (!r.isSuccess) {
          alert(r.message);
        }
      });
    }
  }
}
</script>

<style lang="scss">
  #panel-api-key {
    .mk-panel-body {
      .mk-info {
        margin-top: 21px!important;
      }
    }
  }
  .two-factor-cunfirm {
    padding: 20px 10px 10px 10px;

    .app-links {
      padding: 20px 0 20px 0;
    }
  }
</style>



